import React from 'react';
import { Link } from 'react-router-dom';
function Navbar() {
  return (<div>
    <nav className="navbar navbar-expand-lg ">
  <div className="container-fluid">
    <a className="navbar-brand" href="/">Shopping mall</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active mx-10" aria-current="page" href="/"><Link to="/abn">View Button</Link></a>
        </li>
        <li className="nav-item">
          <a className="nav-link mx-5" href="/">Link</a>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle mx-10" href="/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Dropdown
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a className="dropdown-item" href="/">Action</a></li>
            <li><a className="dropdown-item" href="/">Another action</a></li>

            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="/">Another action</a></li>

            <li><a className="dropdown-item" href="/">Another action</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="/">Something else here</a></li>
          </ul>
        </li>
      </ul><div>
      <form className="d-flex" role="search">
        <input className="form-control me-2 " type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success mx" type="submit">Search</button>
      </form></div>
      <li >
          <h6  className='nav-item mx-3 text-center'>Mall Timings:9:30 - 10:30</h6>
        </li>
    </div>
  </div>
</nav>
</div>
  );
}
export default Navbar;
